import { Query } from 'react-apollo'
import Error from './ErrorMessage'
import gql from 'graphql-tag'

const possiblePermissions = [
  'ADMIN',
  'USER',
  'ITEMCREATE',
  'ITEMUPDATE',
  'ITEMDELETE',
  'PERMISSIONUPDATE'
]

const ALL_USERS_QUERY = gql`
  query {
    users {
      id
      name
      email
      permissions
    }
  }
`

const Permissions = props => (
  <Query query={ALL_USERS_QUERY}>
    {({ data, loading, error }) =>
      console.log(data) || (
        <div>
          <Error error={error} />
          <div>
            <h4>Permissions</h4>
            <table>
              <thead>
                <tr>
                  <th>Name</th>
                  <th>Email</th>
                  {possiblePermissions.map(permission => (
                    <th>{permission}</th>
                  ))}
                  <th>üëáüèª</th>
                </tr>
              </thead>
              <tbody>
                {data.users.map(user => (
                  <User user={user} />
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )
    }
  </Query>
)

class User extends React.Component {
  render() {
    const user = this.props.user
    return (
      <tr>
        <td>{user.name}</td>
        <td>{user.email}</td>
        {possiblePermissions.map(permission => (
          <td>
            <label htmlFor={`${user.id}-permission-${permission}`}>
              <input type="checkbox" />
            </label>
          </td>
        ))}
        <td>
          <button className="inherit save">Update</button>
        </td>
      </tr>
    )
  }
}

export default Permissions
